import React from "react";

import filmjson from "../../assets/Checkpoint2.json";

function List() {
  const fisar = filmjson;

  return (
    <div>
      <h1 className="pageTitel">Hand full av Sci-fi!</h1>
      <div className="posts">
        {fisar.map((post) => (
          <div className="filmWrap" key={post.titel}>
            <h1 className="filmNamn">{post.titel}</h1>
            <h3>Genre: {post.genre}</h3>
            <h3>imdb rating: {post.imdbrating}</h3>
            

            <h3 className="item">LedarDirector: {post.director.map((sub)=>
            (<p className="sub">{sub.LedarDirector}</p>)
            )}</h3>
            
            <h3 className="item">AprenticeDirector: {post.director.map((sub)=>
            ( <p className="sub">{sub.AprenticeDirector}</p>)
            )}</h3>



          </div>
        ))}
      </div>
    </div>
  )
}
export default List;
